


















export default {
    data() {
        return {
            src:"",
			control:{
				showCamera:true
			},
			result:"welcome...."
        }
    },
    methods: {
		takePhoto(){
			const ctx = wx.createCameraContext()
			ctx.takePhoto({
			  quality: 'high',
			  success: (res) => {
				  this.control.showCamera = false
				  this.src = res.tempImagePath
			  }
			})
		},
		recognise(flag){
			switch(flag){
				case 1:
					//todo
					this.result="result1"
					break;
				case 2:
					//todo
					this.result="result2"
					break;
				case 3:
					//todo
					this.result="result3"
					break;
			}
		}
    },
}
